.wrap {
  display: flex;
  gap: 10px;
  flex-direction: column;
}

.textarea__wrapper {
  display: flex;
  gap: 1em;
  align-items: flex-end;
  justify-content: flex-start;
  margin-bottom: 1em;
}

textarea {
  resize: none;
  width: 400px;
  height: 100px;
  border: 1px solid #9d9d9d;
  border-radius: 10px;
  padding: 10px 20px;
  font: 18px/1.3 'Arial';
}

textarea:focus-visible {
  outline: none;
}

.error {
  color: red;
  font-weight: 700;
}

button {
  background: rgba(234,234,234,0.8);
  border-radius: 10px;
  border: 1px solid #9d9d9d;
  text-transform: uppercase;
  color: #000;
  display: flex;
  align-items: center;
  padding: 10px 20px;
  cursor: pointer;
}