.post {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 1em;
  width: 100%;
  border-bottom: 1px solid rgba(234,234,234,0.2);
  padding: 1em 0;
}

.post img {
  border-radius: 50%;
  overflow: hidden;
  width: 70px;
  height: 70px;
}

.text {
  font-size: 15px;
  font-weight: 500;
  color: #0d0d0d;
  width: 50%;
}

.likesCount {
  margin-top: auto;
  height: 100%;
  font: 500 12px/1 "Arial";
}