.profile {
  background: rgba(82, 78, 183, 0.3);
  padding: 1em;
}

.statusInput {
  display: block;
  border: none;
  padding: 0;
  margin: 0;
  background: transparent;
  font: 500 14px/1.3 "Times New Roman";
}

input[type="text"]:focus,
input[type="text"]:focus-visible{
  border: none;
  outline: none
}

.statusDiv {
  font: 500 14px/1.3 "Times New Roman";
}

.textInput {
  padding: 4px 5px;
  border: none;
  width: 220px;
  border-radius: 3px;
}

.lookingForAJobDescription {
  position: absolute;
  left: calc(100% + 100px);
  top: 0;
}

.checkboxRow {
  width: 350px;
  position: relative;
}

.inputRow:not(:last-of-type) {
  margin-bottom: 10px;
}

.textLabel {
  display: inline-block;
  margin-right: 10px;
  width: 150px;
}

.submit {
  margin-top: 15px;
}